.Count-window {
  background-color: #241D1D;
  color: greenyellow;
  font-size: 1.4rem;
  border-radius: 5px;
  width: 30%;
  height: 40px;
  margin: 4px auto 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center; 
}

.Count-window > span.on {
  opacity: 1;
  -webkit-transition: all 2s;
  -o-transition: all 2s;
  transition: all 2s;
}

.Count-window > span.off {
  opacity: 0;
  -webkit-transition: all 2s;
  -o-transition: all 2s;
  transition: all 2s;  
}

.Count > h3 {
  text-align: center;
  font-size: 1.2rem;
}


